.help-guide-overlay {
  z-index: 9998;
  pointer-events: none;
  background-color: #000000b3;
  animation: .3s ease-out fadeIn;
  position: fixed;
  inset: 0;
}

.help-guide-tooltip {
  z-index: 9999;
  background: var(--glass-bg);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  background-image: linear-gradient(white, white), var(--aurora-gradient-accent);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-2xl), 0 0 60px #6366f14d;
  padding: var(--space-xl);
  pointer-events: auto;
  transform-origin: center;
  background-origin: border-box;
  background-clip: padding-box, border-box;
  border: 2px solid #0000;
  min-width: 320px;
  max-width: 420px;
  animation: .4s cubic-bezier(.34, 1.56, .64, 1) helpSlideIn;
  position: absolute;
}

@keyframes helpSlideIn {
  from {
    opacity: 0;
    transform: scale(.9)translateY(-10px);
  }

  to {
    opacity: 1;
    transform: scale(1)translateY(0);
  }
}

.help-guide-tooltip h5 {
  background: var(--aurora-gradient-accent);
  -webkit-text-fill-color: transparent;
  margin: 0 0 var(--space-md) 0;
  align-items: center;
  gap: var(--space-sm);
  background-clip: text;
  font-size: 1.35rem;
  font-weight: 700;
  line-height: 1.3;
  display: flex;
}

.help-guide-tooltip h5:before {
  content: "✨";
  -webkit-text-fill-color: initial;
  background: none;
  font-size: 1.5rem;
}

.help-guide-tooltip p {
  margin-bottom: var(--space-lg);
  color: var(--text-primary);
  font-size: 1rem;
  line-height: 1.6;
}

.help-guide-progress {
  background: var(--bg-secondary);
  border-radius: var(--radius-full);
  width: 100%;
  height: 6px;
  margin-bottom: var(--space-lg);
  position: relative;
  overflow: hidden;
}

.help-guide-progress-bar {
  background: var(--aurora-gradient-accent);
  border-radius: var(--radius-full);
  height: 100%;
  transition: width .4s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  overflow: hidden;
}

.help-guide-progress-bar:after {
  content: "";
  background: linear-gradient(90deg, #0000, #ffffff4d, #0000);
  animation: 2s infinite shimmer;
  position: absolute;
  inset: 0;
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(100%);
  }
}

.help-guide-buttons {
  gap: var(--space-sm);
  margin-top: var(--space-lg);
  flex-direction: column;
  display: flex;
}

.help-guide-nav-group {
  justify-content: space-between;
  align-items: center;
  gap: var(--space-sm);
  width: 100%;
  display: flex;
}

.help-guide-step-indicator {
  color: var(--text-secondary);
  white-space: nowrap;
  background: var(--bg-secondary);
  border-radius: var(--radius-md);
  text-align: center;
  min-width: 80px;
  padding: 6px 12px;
  font-size: .875rem;
  font-weight: 600;
}

.help-guide-nav-group .btn {
  border-radius: var(--radius-md);
  min-width: 80px;
  transition: all var(--transition-base);
  flex: 1;
  font-weight: 600;
}

.help-guide-buttons .btn-outline-danger {
  width: 100%;
  font-weight: 600;
}

.help-guide-shortcuts {
  justify-content: center;
  align-items: center;
  gap: var(--space-md);
  margin-top: var(--space-md);
  padding-top: var(--space-md);
  border-top: 1px solid var(--border-color);
  color: var(--text-secondary);
  flex-wrap: wrap;
  font-size: .85rem;
  font-weight: 500;
  display: flex;
}

.help-guide-shortcuts kbd {
  border-radius: var(--radius-sm);
  color: #333;
  text-shadow: 0 1px #fffc;
  text-align: center;
  background: linear-gradient(135deg, #f0f0f0, #e0e0e0);
  border: 1px solid silver;
  border-bottom-width: 3px;
  min-width: 28px;
  padding: 5px 10px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, monospace;
  font-size: .85rem;
  font-weight: 600;
  display: inline-block;
  box-shadow: 0 2px #b0b0b0, 0 3px 4px #00000026;
}

.target-highlight {
  z-index: 9999;
  pointer-events: auto;
  border-radius: var(--radius-md);
  filter: brightness(1.1) contrast(1.1);
  background-clip: padding-box;
  animation: 2s ease-in-out infinite spotlight-pulse;
  position: relative;
  box-shadow: 0 0 0 4px #6366f1cc, 0 0 0 8px #6366f199, 0 0 0 12px #6366f166, 0 0 60px #6366f1b3, 0 0 100px #6366f166;
  backdrop-filter: none !important;
}

@keyframes spotlight-pulse {
  0%, 100% {
    filter: brightness(1.1) contrast(1.1);
    box-shadow: 0 0 0 4px #6366f1cc, 0 0 0 8px #6366f199, 0 0 0 12px #6366f166, 0 0 60px #6366f1b3, 0 0 100px #6366f166;
  }

  50% {
    filter: brightness(1.15) contrast(1.15);
    box-shadow: 0 0 0 6px #6366f1e6, 0 0 0 12px #6366f1b3, 0 0 0 18px #6366f180, 0 0 80px #6366f1cc, 0 0 120px #6366f180;
  }
}

.help-guide-welcome-toast {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  width: 360px;
  box-shadow: var(--shadow-2xl);
  border-radius: var(--radius-lg);
  z-index: 9999;
  border: 1px solid var(--glass-border);
  animation: .5s cubic-bezier(.34, 1.56, .64, 1) slideInUp;
  position: fixed;
  bottom: 24px;
  left: 24px;
  overflow: hidden;
}

.help-guide-welcome-header {
  padding: var(--space-md) var(--space-lg);
  background: var(--aurora-gradient-accent);
  color: #fff;
  align-items: center;
  font-weight: 600;
  display: flex;
}

.help-guide-welcome-header i {
  margin-right: var(--space-sm);
  font-size: 1.25rem;
}

.help-guide-welcome-close {
  cursor: pointer;
  color: #fff;
  opacity: .8;
  transition: all var(--transition-fast);
  border-radius: var(--radius-sm);
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  margin-left: auto;
  padding: 0;
  font-size: 1.5rem;
  line-height: 1;
  display: flex;
}

.help-guide-welcome-close:hover {
  opacity: 1;
  background: #fff3;
}

.help-guide-welcome-body {
  padding: var(--space-lg);
  background: var(--bg-primary);
}

.help-guide-welcome-body p {
  color: var(--text-secondary);
  margin-bottom: var(--space-md);
  line-height: 1.5;
}

.help-guide-welcome-body p:last-of-type {
  margin-bottom: var(--space-lg);
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(100px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.help-guide-tooltip:after {
  content: "";
  pointer-events: none;
  border: 10px solid #0000;
  width: 0;
  height: 0;
  position: absolute;
}

.help-guide-tooltip[data-placement="top"]:after {
  border-top-color: var(--bg-primary);
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
}

.help-guide-tooltip[data-placement="bottom"]:after {
  border-bottom-color: var(--bg-primary);
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
}

.help-guide-tooltip[data-placement="left"]:after {
  border-left-color: var(--bg-primary);
  top: 50%;
  right: -20px;
  transform: translateY(-50%);
}

.help-guide-tooltip[data-placement="right"]:after {
  border-right-color: var(--bg-primary);
  top: 50%;
  left: -20px;
  transform: translateY(-50%);
}

@media (width <= 768px) {
  .help-guide-tooltip {
    min-width: auto;
    max-width: calc(100vw - 32px);
    padding: var(--space-lg);
    width: auto !important;
    left: 16px !important;
    right: 16px !important;
  }

  .help-guide-welcome-toast {
    width: calc(100% - 32px);
    bottom: 16px;
    left: 16px;
  }

  .help-guide-nav-group .btn {
    padding: 8px 12px;
    font-size: .875rem;
  }

  .help-guide-step-indicator {
    min-width: 60px;
    padding: 4px 8px;
    font-size: .75rem;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.help-guide-tooltip.step-transition {
  animation: .3s ease-out stepTransition;
}

@keyframes stepTransition {
  0% {
    opacity: 1;
    transform: scale(1);
  }

  50% {
    opacity: .5;
    transform: scale(.98);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

[data-theme="dark"] .help-guide-tooltip {
  border-color: var(--glass-border);
  background: #0f172ae6;
}

[data-theme="dark"] .help-guide-tooltip h5 {
  color: var(--aurora-primary-light);
}

[data-theme="dark"] .help-guide-tooltip p {
  color: var(--text-secondary);
}

[data-theme="dark"] .help-guide-welcome-toast {
  background: #0f172af2;
}

[data-theme="dark"] .help-guide-welcome-body {
  background: var(--bg-secondary);
}

@media (prefers-reduced-motion: reduce) {
  .help-guide-tooltip, .help-guide-overlay, .help-guide-welcome-toast, .target-highlight {
    transition: none !important;
    animation: none !important;
  }
}

.help-guide-tooltip .btn:focus-visible {
  outline: 3px solid var(--aurora-primary);
  outline-offset: 2px;
}
